    methods: {
        exam: function (x) {
            if (this.word.options[x].right === true) {
                if (this.key == -1 && this.key2 == -1) {
                    this.countRight += 1;
                }
                this.key = x;
            } else {
                this.key2 = x;
                let rightOne = -1;
                for (var i = 0; i < this.word.options.length; i++) {
                    if (this.word.options[i].right === true) {
                        rightOne = i
                    }
                }
                this.key = rightOne;
            }
            this.angry = false;
            var audio = new Audio(this.word.audioUrl);
            console.log(audio);
            audio.play();
        },
        next: function () {
            this.number2++;
            if (this.number < this.total - 1) {
                this.key = -1;
                this.key2 = -1;
                this.number += 1;
                this.word = this.words[this.number];
            }
            console.log(this.number);
            console.log(this.total);
            if (this.number2 >= this.total) {
                this.angry = true;
            }
        }
    },
    computed: {
        total: function () {
            return this.words.length
        }
    }
}
