    font-size: 1.2em;
}
.answer {
margin-bottom: 6%;
}
.answer .option{
    cursor: pointer;
    font-weight: bold;
    border: 3px solid #fff;
    margin: 3% 0;
    padding: 2%;
    border-radius: 5px;
}
.option:hover{
    background-color: #3b9bf5;
}

.answer .correct{
    background-color: #3BB273;
    background-image: url("../assets/right.png");
    background-size: 32px;
    background-position: 96%;
    background-repeat: no-repeat;
}
.answer .wrong{
    background-color: #ed475c ;
    background-image: url("../assets/wrong.png");
    background-size: 28px;
    background-position: 96%;
    background-repeat: no-repeat;
}

.next, .count, .tip{
    height: 50px;
    border-radius: 5px;
    font-weight: bold;
}
.next{
    width: 26%;
    cursor: pointer;
    text-align: center;
    padding: 15px;
    background: #1076d2 url("../assets/next.png") no-repeat center;
    background-size: 22px;
    box-shadow: 0 5px 0 0 #00519a;
}
.count{
    width: 26%;
    line-height: 50px;
    padding: 0 2%;
    background-color: #3BB273;
    box-shadow: 0 5px 0 0 #1b4b35;
}
.tip{
    text-align: center;
    width: 44%;
    margin-left: 2%;
    line-height: 50px;
    background-color: #ed475c;
    box-shadow: 0 5px 0 0 #562227;
}
.next:hover{
    background-color: #006fb9;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0
}

</style>



