<template>
<div class="wrap">
    <footer-bar></footer-bar>
    <main-body class="main-body"></main-body>
    <header-bar></header-bar>
</div>
</template>

<script>
import mainBody from "./components/main-body.vue"
import headerBar from "./components/header.vue"
import footerBar from "./components/footer.vue"
export default {
    name: 'app',
    components: {
        "mainBody": mainBody,
        "footerBar": footerBar,
        "headerBar": headerBar
    }
}
</script>

<style>
.cf:before,
.cf:after {
    content: "";
    display: table;
}

.cf:after {
    clear: both;
}

.cf {
    zoom: 1;
}

.fl {
    float: left;
}

.fr {
    float: right;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background: -webkit-linear-gradient(top, #1076d2 0%, #66bdff 100%) no-repeat;
    background-size: 4096px 2160px;
    font: 300 1em/1.8 -apple-system, BlinkMacSystemFont, "PingFang SC", "Helvetica Neue", STHeiti, "Microsoft Yahei", Tahoma, Simsun, sans-serif;
}

.main-body {
    margin: 30px auto;
}
</style>



